import React, {useState, useCallback} from 'react'
import AddressInput from './address-input'
import HorairesInput from './horaires-input'

function CCASForm({ commune }) {
  const [adresse, setAdresse] = useState()
  const [telephone, setTelephone] = useState('')
  const [url, setUrl] = useState('')

  const handleAdresseChange = useCallback(value => {
    setAdresse(value)
  })

  const handleTelephoneChange = useCallback(event => {
    setTelephone(event.target.value)
  })

  const handleURLChange = useCallback(event => {
    setUrl(event.target.value)
  })

  const defaultHoraires = {
    du: 'lundi',
    au: 'vendredi',
    heures: [{
      de: '8h',
      a: '12h'
    }, {
      de: '14h',
      a: '17h'
    }]
  }

  const [horaires, setHoraires] = useState([defaultHoraires])
  const save = useCallback(e => {})
  return (
    <div>
      <div className="form__group">
          <label htmlFor="adresse">üåç Adresse du CCAS</label>
          <AddressInput id="adresse" citycode={commune.code} value={adresse} onChange={handleAdresseChange} />
      </div>
      <div className="form__group">
          <label htmlFor="telephone">üì† Num√©ro de t√©l√©phone du CCAS</label>
          <input id="telephone" placeholder="02 40 12 23 34" type="tel" value={telephone} onChange={handleTelephoneChange} />
      </div>
      <div className="form__group">
          <label htmlFor="url">URL vers les informations du CCAS</label>
          <input id="url" type="tel" value={url} onChange={handleURLChange} />
      </div>
      <div className="form__group">
          <label htmlFor="horaires">Horaires d'ouverture du CCAS</label>
          <HorairesInput value={horaires} onChange={setHoraires} />
      </div>
      <div className="form__group">
        <button className="button large" onClick={save}>Enregistrer</button>
      </div>
    </div>
  )
}

export default CCASForm
